.ContactsSocial{
    width: 250px;
    height: 200px;
    background-color: lightgray;
    border-radius: 15px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ContactsSocial h1{
    margin-top: 0;
}


.SocialImageWrap{
    display: flex;
    justify-content: center;
    align-items: center;

}

.SocialLogo{
    width: 80px;
    height: 80px;
}


@media (max-width:1500px) {

.ContactsSocial{
    width: fit-content;
    height: fit-content;
    justify-content: center;
    }

}

@media (max-width:800px) {
    .ContactsSocial{
        margin-top: 10px;
        width: 100%;
    }  
}