.MenuList{

    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin: 0;
    padding: 0;

}

@media (max-width:750px){

    .MenuList{
        width: 100%;
        position: absolute;
        background-color: #002366;
        flex-direction: row;
        transform: translateY(-100%);
        transition: .3s ease;
    }

}