.MessengersSection{
    width:fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.MessengersRepresent{
    margin: 0;
    text-align: center;
}

.MessengersWrap{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: lightgrey;
    border-radius: 25px;
    margin-top: 20px;
    padding: 10px;

}

.MessengerLogo{
    width: 75px;
    height: 75px;
    margin: 0 10px 0 10px;
}


@media (max-width:1500px) {

.MessengerLogo{
    width: 50px;
    height: 50px;
    }
}

@media (max-width:800px) {
.MessengerLogo{
    width: 30px;
    height: 30px;
    }

.MessengersWrap{
    margin-top: 0;
    }

.MessengersRepresent{
    font-size: 10px;
    }
}