.ContactsProf{
    width: 450px;
    height: fit-content;
    background-color: lightgray;
    border-radius: 15px;
    padding: 20px;
    height: 200px;
    text-align: center;
}

@media (max-width:1500px) {

.ContactsProf{
    width: fit-content;
    height: fit-content;
    padding: 10px;
}

}

@media (max-width:800px) {
    .ContactsProf{
        margin-top: 10px;
        width: 100%;
    }  
}