.ContactsInfo{
    text-align: center;
    width: 260px;
    height: 200px;
    background-color: lightgray;
    border-radius: 15px;
    padding: 20px;
}

@media (max-width:1500px) {

    .ContactsInfo{
        width: fit-content;
        height: fit-content;
        padding: 10px;
        font-size: 15px;
    }

}

@media (max-width:800px) {
    .ContactsInfo{
        margin-top: 10px;
        width: 100%;
    }  
}