.Header{
    display: flex;
    background-color: #002366;
    width: 100%;
    min-height: 100px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: relative;
}

@media (max-width:1500px) {

.Header{
    min-height: 80px;
    }
}
    
@media (max-width:800px) {

.Header{
    min-height: 60px;
    }
}